import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

/**
 * Load components async with dynamic import.
 * @see: babel-plugin-syntax-dynamic-import
 *
 * @usage: route level code-splitting,
 * this generates a separate chunk (about.[hash].js) for this route,
 * which is lazy-loaded when the route is visited.
 */
function loadView (view) {
  return () => import(`@/views/${view}.vue`) /* webpackChunkName: "view-[request]" */
}

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      name: 'Home',
      path: '/',
      component: loadView('Home')
    },
    {
      name: 'About',
      path: '/om',
      component: loadView('About')
    },
    {
      name: 'FAQ',
      path: '/spørsmål-og-svar',
      component: loadView('FAQ')
    },
    {
      name: 'Blog',
      path: '/blogg',
      component: loadView('Blog')
    },
    {
      name: 'Post',
      path: '/innlegg/:id',
      component: loadView('Post')
    },
    {
      name: 'Program',
      path: '/program/:id',
      component: loadView('Program')
      // children: [
      //   // these will be rendered inside Scientist's <router-view>
      //   {
      //     path: 'dashboard',
      //     component: loadView('Dashboard')
      //   }
      // ]
    }
  ],
  // scroll to top every route change
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})
