<template>
  <v-layout
    justify-center align-center
    wrap
    pa-3
  >
    <v-flex
      xs12 sm12 md6 lg6 xl6
      pr-5
    >
      <h4 class="headline" style="font-family: Roboto;">
        <!-- Lyst til å lære hvordan du som helt utrent på bare noen uker kan klare å løpe en mil i strekk? -->
        Vil du lære mer om løping og få nyttige råd om hvordan du best mulig kan komme i gang?
      </h4>

      <h4 class="subheading">
        <!-- Få mine beste råd for hva man skal tenke på når man har lyst til å begynne med løping -->
        Meld deg på her
      </h4>
    </v-flex>

    <v-flex xs12 sm12 md3 lg3 xl3>
      <v-text-field
        :rules="[rules.required]"
        :validate-on-blur="true"
        label="Navn"
      ></v-text-field>

      <v-text-field
        :rules="[rules.required, rules.email]"
        :validate-on-blur="true"
        label="Epost"
      ></v-text-field>

      <button class="button submit" tag="button" @click="go">
        <span>Ja takk</span>
      </button>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  data () {
    return {
      name: '',
      email: '',
      rules: {
        required: value => !!value || 'Felt kan ikke være tomt.',
        email: value => {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
          return pattern.test(value) || 'Ugyldig epost.'
        }
      }
    }
  },

  methods: {
    go () {
      // axios('', { name: this.name, this.email })
    }
  }
}
</script>
